// Variables

$black: #93a5b5;
$white: #eaeaea;
$gray: #b4c4d2;

$text-arrow-space: 6px;
$shaft-width: 1px;
$newshaft-width: 64px;
$shaft-thickness: 1px;
$arrow-head-width: 8px;
$arrow-head-thickness: $shaft-thickness;

// Base

* {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.arrow-container {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;

  ul {
    li {
      margin: 0 0 24px;
    }
  }

  // The Arrow

  .the-arrow {
    width: $shaft-width;
    transition: all 0.2s;

    &.-left {
      position: absolute;
      top: 60%;
      left: 0;

      > .shaft {
        width: 0;
        background-color: $black;

        &:before,
        &:after {
          width: 0;
          background-color: $black;
        }

        &:before {
          transform: rotate(0);
        }

        &:after {
          transform: rotate(0);
        }
      }
    }

    &.-right {
      top: 3px;

      > .shaft {
        width: $shaft-width;
        transition-delay: 0.2s;

        &:before,
        &:after {
          width: $arrow-head-width;
          transition-delay: 0.3s;
          transition: all 0.5s;
        }

        &:before {
          transform: rotate(40deg);
        }

        &:after {
          transform: rotate(-40deg);
        }
      }
    }

    > .shaft {
      background-color: $black;
      display: block;
      height: $shaft-thickness;
      position: relative;
      transition: all 0.2s;
      transition-delay: 0;
      will-change: transform;

      &:before,
      &:after {
        background-color: $black;
        content: '';
        display: block;
        height: $arrow-head-thickness;
        position: absolute;
        top: 0;
        right: 0;
        transition: all 0.2s;
        transition-delay: 0;
      }

      &:before {
        transform-origin: top right;
      }

      &:after {
        transform-origin: bottom right;
      }
    }
  }

  // Animated Arrow Button

  .animated-arrow {
    display: inline-block;
    color: $black;
    font-size: 1.25em;
    font-style: italic;
    text-decoration: none;
    position: relative;
    transition: all 0.2s;
    @media (min-width: 978px){
    &:hover {
      color: $gray;

      > .the-arrow.-left {
        > .shaft {
          width: $newshaft-width;
          transition-delay: 0.1s;
          background-color: $gray;

          &:before,
          &:after {
            width: $arrow-head-width;
            transition-delay: 0.1s;
            background-color: $gray;
          }

          &:before {
            transform: rotate(40deg);
          }

          &:after {
            transform: rotate(-40deg);
          }
        }
      }

      > .arrow-main {
        transform: translateX($shaft-width + $text-arrow-space);
        transform: translateX($newshaft-width + $text-arrow-space);

        > .the-arrow.-right {
          > .shaft {
            width: 0;
            transform: translateX(200%);
            transition-delay: 0;

            &:before,
            &:after {
              width: 0;
              transition-delay: 0;
              transition: all 0.1s;
            }

            &:before {
              transform: rotate(0);
            }

            &:after {
              transform: rotate(0);
            }
          }
        }
      }
    }
    }

    > .arrow-main {
      display: flex;
      align-items: center;
      transition: all 0.2s;

      > .text {
        margin: 0 $text-arrow-space 0 0;
        line-height: 1;
        text-align: center;
      }

      > .the-arrow {
        position: relative;
      }
    }
  }
}
